<template>
  <div v-show="isShow" class="audio-enter">
    <van-overlay :show="isShow" @click="close" class-name="custom-overlay">
      <div class="audio-pie" @click.stop>
        <!--  -->
        <div v-if="beginRecoding">
          <div class="audio-pie_audio--times" v-if="nowDuration !== null">
            {{ nowDuration }}
          </div>
          <div v-show="true" class="audio-pie_audio--osc">
            <canvas id="canvas"></canvas>
          </div>
          <div v-if="drawColuList.length > 0" class="audio-pie_audio--osc">
            <div
              class="audio-pie_audio--osc_item"
              v-for="(item, idx) in drawColuList"
              :key="idx"
              :style="{ height: item + 'px' }"
            ></div>
          </div>
        </div>

        <div
          class="audio-pie_btn"
          @touchstart.prevent="touchstart"
          @touchend.prevent="touchend"
          @touchmove.prevent="touchmove"
        >
          <div class="audio-pie_btn-icon">
            <template v-if="beginRecoding">
              <div class="audio-pie_btn-icon1"></div>
              <div class="audio-pie_btn-icon2"></div>
            </template>
            <div v-else class="iconfont icon-Voice"></div>
          </div>

          <div class="audio-pie_txt">
            {{ beginRecoding ? "" : "" }}
          </div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>
<script>
import Recorder from "js-audio-recorder";
const lamejs = require("lamejs");
/**
 * @param isShow 
 * @param closeAudio 
 */
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      beginRecoding: false, // 
      blackBoxSpeak: false, // 
      startY: "", // Y
      timeOutEvent: 0, // 
      waveCanvas: null,
      ctx: null,
      recorder: null, // 
      drawRecordId: null, // id
      nowDuration: null, // 
      limitDuration: 60, // -
      drawColuList: [], // 
    };
  },
  watch: {
    isShow: {
      handler(nv) {
        if (nv) {
          this.getPermission();
        }
      },
      deep: true,
    },
  },
  mounted() {
    // 
    this.recorder = new Recorder({
      sampleBits: 16, //  8  1616
      sampleRate: 48000, //  110251600022050240004410048000
      numChannels: 1, //  1  2 1
      // compiling: false,(0.x,1.x)  // false
    });
    // 
    const vm = this;
    this.recorder.onprogress = function (params) {
      if (Math.floor(params.duration) === this.limitDuration) {
        vm.touchend();
      }
      let d = Math.floor(params.duration);
      d = Number(d) < 10 ? "0" + d : d;
      d = "0:" + d;
      vm.$set(vm, "nowDuration", d);

      // console.log('--------------START---------------')
      // console.log('()', params.duration);
      // console.log('()', params.fileSize);
      // console.log('(%)', params.vol);
      // console.log('([DataView, DataView...])', params.data);
      // console.log('--------------END---------------')
    };

    // this.startCanvas();
  },
  created() {
    this.$nextTick(() => {
      this.getPermission();
    });
  },
  methods: {
    // 
    getPermission() {
      Recorder.getPermission().then(
        () => {
          console.log("");
        },
        (error) => {
          console.log(`${error.name} : ${error.message}`);
        }
      );
    },
    // canvas 
    startCanvas() {
      //
      this.waveCanvas = document.getElementById("canvas");
      this.waveCanvas.width = "145";
      this.waveCanvas.height = 44;
      this.ctx = this.waveCanvas.getContext("2d");
    },
    // 
    touchstart(e) {
      this.blackBoxSpeak = true;
      this.startY = e.clientY;

      this.timeOutEvent = 0;
      // 1000
      this.timeOutEvent = setTimeout(() => {
        this.startRecorder();
        this.beginRecoding = true;
      }, 500);
      return false;
    },
    // 
    touchend() {
      // 
      clearTimeout(this.timeOutEvent);
      if (this.timeOutEvent !== 0) {
        this.beginRecoding = false;
        this.stopRecorder(); // 
        // 
        if (this.blackBoxSpeak) {
          // 
          this.$emit("closeAudio", this.recorder.getWAVBlob());
        }
        //console.log('>>>', this.getRecorder())
        this.nowDuration = null;
        this.drawColuList = [];
      }
    },
    // 
    touchmove(e) {
      const endY = e.clientY;
      this.blackBoxSpeak = this.startY < endY;
    },
    //x-- 
    startRecorder() {
      this.recorder.start().then(
        () => {
          this.beginRecoding = true;
          //this.drawRecordWave();//
          this.drawRecordColu(); // 
        },
        (error) => {
          console.log(`${error.name} : ${error.message}`);
        }
      );
    },
    // 
    resumeRecorder() {
      this.recorder.resume();
    },
    // 
    pauseRecorder() {
      this.recorder.pause();
      this.drawRecordId && cancelAnimationFrame(this.drawRecordId);
      this.drawRecordId = null;
    },
    // 
    stopRecorder() {
      this.recorder.stop();
      this.drawRecordId && cancelAnimationFrame(this.drawRecordId);
      this.drawRecordId = null;
    },
    // 
    playRecorder() {
      console.log(123132);
      this.recorder.play();
      this.drawPlay(); //
    },
    // 
    pausePlayRecorder() {
      this.recorder.pausePlay();
    },
    // 
    resumePlayRecorder() {
      this.recorder.resumePlay();
      this.drawPlay(); //
    },
    // 
    stopPlayRecorder() {
      this.recorder.stopPlay();
    },
    // 
    destroyRecorder() {
      const vm = this;
      if (vm.recorder) {
        vm.recorder.destroy().then(function () {
          vm.recorder = null;
          vm.drawRecordId && cancelAnimationFrame(vm.drawRecordId);
          vm.drawRecordId = null;
        });
      }
    },
    // 
    getRecorder() {
      let map = new Map();
      map.set("duration", this.recorder.duration); //
      map.set("fileSize", this.recorder.fileSize); //

      //
      map.set("PCMBlob", this.recorder.getPCMBlob()); // PCM 
      map.set("wavBlob", this.recorder.getWAVBlob()); // WAV 
      map.set("mp3Blob", this.convertToMp3(this.recorder.getWAV()));
      map.set("channel", this.recorder.getChannelData()); //
      return map;
    },
    // pcm
    downPCM() {
      this.recorder.downloadPCM(""); //
    },
    // wav
    downWAV() {
      this.recorder.downloadWAV(""); //
    },
    //  wav-map3
    downMP3() {
      const mp3Blob = this.convertToMp3(this.recorder.getWAV());
      this.recorder.download(mp3Blob, "recorder", "mp3");
    },
    // wavmp3
    convertToMp3(wavDataView) {
      // wav
      const wav = lamejs.WavHeader.readHeader(wavDataView); // wavconfig
      const { channels, sampleRate } = wav;
      const mp3enc = new lamejs.Mp3Encoder(channels, sampleRate, 128);
      // 
      const result = this.recorder.getChannelData();
      const buffer = [];

      const leftData =
        result.left &&
        new Int16Array(result.left.buffer, 0, result.left.byteLength / 2);
      const rightData =
        result.right &&
        new Int16Array(result.right.buffer, 0, result.right.byteLength / 2);
      const remaining = leftData.length + (rightData ? rightData.length : 0);

      const maxSamples = 1152;
      for (let i = 0; i < remaining; i += maxSamples) {
        const left = leftData.subarray(i, i + maxSamples);
        let right = null;
        let mp3buf = null;

        if (channels === 2) {
          right = rightData.subarray(i, i + maxSamples);
          mp3buf = mp3enc.encodeBuffer(left, right);
        } else {
          mp3buf = mp3enc.encodeBuffer(left);
        }

        if (mp3buf.length > 0) {
          buffer.push(mp3buf);
        }
      }
      const enc = mp3enc.flush();
      if (enc.length > 0) {
        buffer.push(enc);
      }
      return new Blob(buffer, { type: "audio/mp3" });
    },
    // 
    drawRecordWave() {
      // requestAnimationFrame60fps
      this.drawRecordId = requestAnimationFrame(this.drawRecordWave);
      // 
      let dataArray = this.recorder.getRecordAnalyseData();
      //console.log('>>>',dataArray)
      let bufferLength = dataArray.length;

      // 
      this.ctx.fillStyle = "rgb(0,0,0)";
      this.ctx.fillRect(0, 0, this.waveCanvas.width, this.waveCanvas.height);

      // 
      this.ctx.lineWidth = 2;
      this.ctx.strokeStyle = "rgb(109, 212, 0)";

      this.ctx.beginPath();
      let sliceWidth = (this.waveCanvas.width * 1.0) / bufferLength; // bufferLength
      let x = 0; // x
      for (var i = 0; i < bufferLength; i++) {
        var v = dataArray[i] / 128.0;
        var y = (v * this.waveCanvas.height) / 2;
        if (i === 0) {
          // 
          this.ctx.moveTo(x, y);
        } else {
          // 
          this.ctx.lineTo(x, y);
        }
        // 
        x += sliceWidth;
      }

      this.ctx.lineTo(this.waveCanvas.width, this.waveCanvas.height / 2);
      this.ctx.stroke();
    },
    // 
    drawRecordColu() {
      // requestAnimationFrame60fps()
      this.drawRecordId = requestAnimationFrame(this.drawRecordColu);
      // 
      let dataArray = this.recorder.getRecordAnalyseData();
      let transit = [];
      this.splitArr([...dataArray], transit);

      let rstArr = [];
      for (let i = 0; i < transit.length; i++) {
        rstArr.push(Math.max(...transit[i]));
      }
      this.drawColuList = [];
      for (var i = 0; i < rstArr.length; i++) {
        // var v = rstArr[i] / 128.0;
        // var h = v * this.waveCanvas.height / 3;
        // this.drawColuList.push(h)
        // 
        let newDb = rstArr[i];
        let waveH = 10;
        if (newDb >= 128 && newDb <= 140) {
          waveH = 15;
        } else if (newDb >= 141 && newDb <= 160) {
          waveH = 20;
        } else if (newDb >= 161 && newDb <= 180) {
          waveH = 25;
        } else if (newDb >= 181 && newDb <= 200) {
          waveH = 30;
        } else if (newDb > 200) {
          waveH = 35;
        }
        this.drawColuList.push(waveH);
      }
      // console.log(this.drawColuList)
      this.$forceUpdate();
    },
    // 
    splitArr(arr, rst, idx) {
      if (!arr || arr.length === 0) {
        return;
      }
      rst.push(arr.splice(0, idx ? idx : 32));
      this.splitArr(arr, rst);
    },
    // 
    arrAverageNum2(arr) {
      let sum = eval(arr.join("+"));
      return ~~((sum / arr.length) * 100) / 100;
    },
    // 
    close() {
      this.$emit("closeAudio");
    },
    // 
    beforeDestroy() {
      this.destroyRecorder();
    },
  },
};
</script>
<style scoped lang="scss">
.audio-enter {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 999999999;
  background-color: #ffffff;

  //   .custom-overlay {
  //     // background-color: rgba(0, 0, 0, 0.8);
  //   }

  .audio-pie {
    z-index: 999;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 74px;
    text-align: center;

    .audio-pie_txt {
      height: 20px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #ffffff;
      line-height: 20px;
      margin-top: 20px;
    }

    .audio-pie_audio--times {
      height: 20px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #ffffff;
      line-height: 20px;
    }
    .audio-pie_audio--osc {
      width: 157px;
      height: 44px;
      //      background: #6DD400;
      margin: 24px auto 0;
      display: flex;
      align-items: center;

      .audio-pie_audio--osc_item {
        width: 2px;
        background-color: rgba(109, 212, 0, 1);
        border-radius: 1000px;

        &:not(:first-child) {
          margin-left: 3px;
        }
      }
    }
    .audio-pie_btn {
      margin-top: 60px;

      .audio-pie_btn-icon {
        position: relative;
        width: 60px;
        height: 60px;
        background: #6dd400;
        border-radius: 100%;
        margin: 0 auto;

        .audio-pie_btn-icon1 {
          position: absolute;
          top: 12px;
          left: 0;
          right: 0;
          margin: 0 auto;
          width: 36px;
          height: 36px;
          border: 4px solid #fff;
          border-radius: 100%;
        }
        .audio-pie_btn-icon2 {
          position: absolute;
          top: 22px;
          left: 0;
          right: 0;
          margin: 0 auto;
          width: 16px;
          height: 16px;
          background-color: #fff;
          border-radius: 100%;
        }

        .iconfont {
          color: #ffffff;
          font-size: 35px;
          line-height: 60px;
        }
      }
    }
  }
}
</style>
